.check {
  padding: 10px;
  padding-left: 60px;
  font-size: 13px;
  cursor: pointer;
}

.check:hover {
  background: #f1fcff;
}

.checkInput {
  -webkit-appearance: none;
  -moz-appearance: none;
}

.checkInput:checked + .checkBox {
  background-image: url('../../img/checked.svg');
}

.checkBox {
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url('../../img/off.svg');
  margin-left: -30px;
}
