@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap');

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Open Sans';
  background-color: #f3f7fa;
}

p {
  margin: 0;
}
