.stopFilter {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 232px;
  padding: 20px 0;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  background: #ffffff;
  margin-right: 20px;
}

.title {
  align-self: center;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 25px;
}
