.ticket {
  display: flex;
  flex-direction: column;
  min-width: 502px;
  padding: 22px 20px 0;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin-bottom: 20px;
  background-color: #ffffff;
}

.topRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 26px;
}

.price {
  font-size: 24px;
  color: #2196f3;
  font-weight: 600;
}

.logo {
  width: 110px;
  height: 36px;
  margin-right: 30px;
}
